// ref
export const getCountry = (params) =>
    Axios.request({
        url: URL.GETCOUNTRY,
        //  url: URL.GETCOUNTRY+`?pageIndex=${params.pageIndex}&pageSize=${params.pageSize}`,
        method: 'get',
    });

