// 
export const saveAnti = (params) =>
    Axios.request({
        url: URL.SAVEANTI,
        data: params,
        method: 'POST',
    });

// 
export const getManager = (params) =>
    Axios.request({
        url: URL.GETMANAGER + `?storeId=${params.storeId}`,
        method: 'get',
        headers_anti: {
            tenantId: params.yhtTenantId,
            yht_access_token: params.yht_access_token,
        },
    });

