import CryptoJS from 'crypto-js'

/* 
    AES 
*/
const aseKey = 'Mima1234'
export function aesEncrypt(v) {
    if (!v) {
        return false
    }

    //
    let encJson = CryptoJS.AES.encrypt(JSON.stringify(v), CryptoJS.enc.Utf8.parse(aseKey), {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7
    }).toString()

    //base64, utf8base64
    let encData = CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(encJson));
    return encData
}
/* 
    AES 
*/
export function aesDecode(v) {
    if (!v) {
        return false
    }

    //base64utf8
    let decData = CryptoJS.enc.Base64.parse(v).toString(CryptoJS.enc.Utf8);

    //
    let decJson = CryptoJS.AES.decrypt(decData, CryptoJS.enc.Utf8.parse(aseKey), {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7
    }).toString(CryptoJS.enc.Utf8)

    return JSON.parse(decJson)
}

